export default {
	created() {
		var neirong = this.questionList[this.number-1]
		if(this.tag == "l"){
			neirong = this.questionList[this.qNumber]
		}
		this.content = neirong.content
		this.jiexi = neirong.jiexi
		this.rightAnswer = neirong.judgementanswer
		if(this.rightAnswer == "right"){
			this.rightAnswer = "对"
		}else{
			this.rightAnswer = "错"
		}
	},
	data() {
		return {
			content: '',
			isActive: 0,
			jiexi: '',
			rightAnswer: ''
		}
	},
	methods: {
		changeActive(e){
			this.isActive = e
		},
		changeQuestion(e){
			var data ={}
			var judge = ''
			if(this.isActive == 1){
				judge = 'right'
			}
			if(this.isActive == 2){
				judge = 'false'
			}
			if(this.tag == "l"){
				data = {
					ps: e
				}
			}else {
				if(this.nextText == "提交"){
					data = {
						ps: e,
						number: this.number-1,
						answer: judge,
						submit: true,
						tixing: this.questionList[this.number-1].tixing
					}
				}else{
					data = {
						ps: e,
						number: this.number-1,
						answer: this.isActive,
						submit: false,
						tixing: this.questionList[this.number-1].tixing
					}
				}
			}	
			this.$emit('func', data);
		}
	},
	props:[
		"number",
		"questionList",
		"nextText",
		"jiexiTag",
		"qNumber",
		"tag"
	]
}
